interface Resultado {
    statusToken: boolean;
    message:string;
    route:string;
}
export const ErrorAxios = (statusCode: number|undefined) => {
    let resultado: Resultado = {
        statusToken: false,
        message:'Error inesperado',
        route:"/login"
    }
    switch (statusCode) {
        case 401:
            resultado.statusToken=false;
            resultado.message='No autorizado';
            resultado.route="/login";
            break;
        case 500:
            resultado.statusToken=true;
            resultado.message='Error inesperado';
            resultado.route="";
            break;
        default:
            resultado.statusToken=true;
            resultado.message='No autorizado';
            resultado.route="/Error";
            break;
    }
    return resultado;
}